  0% {
    transform: scale(0.85);
    box-shadow: 0 0 0 0 rgba(255, 64, 80, 0.7);
  }
  70% {
    transform: scale(1);
  }
  99% {
    box-shadow: 0 0 0 10px transparent;
  }
  100% {
    transform: scale(0.85);
    box-shadow: 0 0 0 0 transparent;
  }
}
