.priceSwicher .toggle {
    position: relative;
    width: 40px;
    height: 20px;
    margin: 0 10px;
    border-radius: 100px;
    background-color: #ff4051;
    overflow: hidden;
    box-shadow: inset 0 0 2px 1px rgba(0, 0, 0, .05)
}

.te-theme .priceSwicher .toggle {
    background-color: #037bfe;
}

.priceSwicher .check {
    margin: 0;
    position: absolute;
    display: block;
    cursor: pointer;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    opacity: 0;
    z-index: 6
}

.priceSwicher .check:checked~.switch {
    right: 4px;
    left: 57.5%;
    transition: .25s cubic-bezier(.785, .135, .15, .86);
    transition-property: left, right;
    transition-delay: .08s, 0s
}

.priceSwicher .switch {
    position: absolute;
    left: 4px;
    top: 4px;
    bottom: 4px;
    right: 57.5%;
    background-color: #fff;
    border-radius: 36px;
    z-index: 1;
    transition: .25s cubic-bezier(.785, .135, .15, .86);
    transition-property: left, right;
    transition-delay: 0s, .08s;
    box-shadow: 0 1px 2px rgba(0, 0, 0, .2)
}

