    justify-content: center;
    position: relative
}

.tb_filter_circular_list {
    padding: 6px 10px;
    position: relative
}

.tb_filter_circular_list:after {
    background-color: #0006;
    bottom: auto;
    content: "";
    height: 1px;
    left: 0;
    margin: 0 auto;
    overflow: hidden;
    position: absolute;
    right: 0;
    top: calc(50% - .5px);
    width: 100%;
    z-index: 1
}

