.progress_sentiment .progress-bar {
  position: relative;
  overflow: inherit;
  border-radius: 20px;
  min-width: 16px;
}

.progress_label {
  position: absolute;
  background-color: #fff;
  padding: 5px;
  border: 1px solid;
  border-color: #545454;
  bottom: 120%;
  border-radius: 3px;
  right: -25px;
  left: auto;
  opacity: 0;
  visibility: hidden;
  transition: all .1s linear;
}


.progress_sentiment:hover .progress_label {
  opacity: 1;
  right: -35px;
  visibility: visible;
}

.progress_label span {
  display: table;
  color: #545454;
  padding: 10px;
}

