.review-hub {
    border-radius: 6px;
    margin-left: 10px;
    margin-right: 10px;
    box-shadow: 0px 0px 8px 0px #0000001F;
    height: calc(100vh - 130px);
}

/* Action-START */
.review-action {
    width: 25%;
    border-radius: 6px 0 0 6px;
    background-color: #fff;
}

.ra-presha {
    width: 75%;
}

.ra-title {
    padding: 10px 15px;
    border-bottom: 1px solid #DBE1E5;
    ;
}

.ra-title p {
    font-weight: 500;
    color: #2b2b2b;
}

.ra-tab {
    padding: 15px;
    padding-bottom: 0;
    background-color: #f7f7fb;
}

.ra-tab-wrap {
    border-radius: 6px;
    border: 1px solid #DBE1E5;
    background: #fff;
    padding: 3px;
}

.ra-tab span {
    color: #878787;
    font-size: 13px;
    font-weight: 500;
    width: 50%;
    text-align: center;
    display: inline-block;
    padding: 7px 10px;
}

.ra-tab span em {
    font-style: normal;
}

.ra-tab span:hover {
    cursor: pointer;
}

.ra-tab span.active {
    position: relative;
    background: #19232E;
    color: #fff;
    border-radius: 4px;
}

.ra-tab span.active:hover {
    cursor: inherit;
}

.ra-tab span svg {
    max-height: 20px;
    margin-right: 5px;
    vertical-align: middle;
}

.ra-tab span.active svg rect {
    fill: transparent;
}

.ra-tab span.active svg path {
    fill: white;
}

.ra-formwrap {
    background: #F7F7FB;
    padding: 15px;
}

.ra-actions {
    padding: 15px;
}

.ra-formwrap .form-control {
    border: 0;
    background: transparent;
    padding: 0;
    margin-bottom: 10px;
}

.ra-formwrap .form-control label {
    font-size: 13px;
    color: #626B76;
    font-weight: 500;
    display: block;
    text-transform: capitalize;
}

.ra-formwrap .form-control input[type="text"]::-webkit-input-placeholder,
.ra-formwrap .form-control textarea::-webkit-input-placeholder {
    color: #96A2B1;
    opacity: 0.8;
}

.ra-formwrap .form-control input[type="text"]::-moz-placeholder,
.ra-formwrap .form-control textarea::-moz-placeholder {
    color: #96A2B1;
    opacity: 0.8;
}

.ra-formwrap .form-control input[type="text"]:-ms-input-placeholder,
.ra-formwrap .form-control textarea:-ms-input-placeholder {
    color: #96A2B1;
    opacity: 0.8;
}

.ra-formwrap .form-control input[type="text"]:-moz-placeholder,
.ra-formwrap .form-control textarea:-moz-placeholder {
    color: #96A2B1;
    opacity: 0.8;
}

.ra-formwrap .form-control input[type="text"],
.ra-formwrap .form-control textarea {
    font-size: 13px;
    font-weight: 500;
    background-color: #fff;
    padding: 10px 15px;
    border: 0;
    color: #333;
    width: 100%;
    border-radius: 2px;
}

.ra-formwrap .form-control textarea {
    resize: none;
    border-radius: 4px;
}

.ra-formwrap .checkbox.checkbox-outline.checkbox-primary>input:checked~span,
.ra-formwrap .checkbox.checkbox-outline.checkbox-primary>input:checked~span:after {
    border-color: transparent;
}

.ra-formwrap .switch input:empty~span {
    margin: 0;
    height: 21px;
}

.ra-formwrap .switch input:empty~span:after {
    width: 14px;
    height: 14px;
    left: 0px;
}

.ra-formwrap .form-control input[type="text"]:focus,
.ra-formwrap .form-control textarea:focus {
    box-shadow: 0 0 10px #f1f1f1;
    outline: none;
}

.qr-code-section .btn-submit,
.qr-code-section .btn-submit:hover {
    box-shadow: 0px 1.05px 0px 0px #0000000D;
    background-color: #1A232F;
    color: #fff;
    border-radius: 4px;
    transition: all 0.3s ease-in-out;
    pointer-events: none;
}

.qr-code-section .btn-submit:hover {
    opacity: 0.9;
}

/* Share-START */
.review-share {
    width: 50%;
    padding: 15px;
    padding-right: 40px;
    padding-top: 80px;
    border-radius: 0 6px 6px 0;
    background-color: #F7F7FB;
}

.hashtag_social_icons i {
    color: #626B76;
    font-size: 20px;
}

.image-upload-control label {
    font-size: 13px;
    font-weight: 500;
    color: #626B76;
}

.image-upload-control .fc-uploader,
.image-upload-control .fc-uploaded {
    background-color: #fff;
    border: 1px dashed #626B76;
    border-radius: 4px;
    padding: 5px 15px;
    min-height: 46px;
    max-height: 46px;
}

.image-upload-control .fc-uploaded {
    padding: 7px 15px;
    display: flex;
    align-items: center;
}

.image-upload-control .fc-uploader:hover {
    cursor: pointer;
}

.image-upload-control .fc-uploaded img {
    max-width: 100%;
    max-height: 25px;
    margin: 0 auto;
}

.image-upload-control .fc-uploaded-inn {
    max-width: 100%;
    margin: 0 auto;
    position: relative;
    text-align: center;
    background: #f9f9f9;
    border-radius: 4px;
    padding: 5px;
}

.image-upload-control .fc-uploaded .close {
    position: absolute;
    right: -5px;
    top: -5px;
    background: #fff;
    width: 14px;
    height: 14px;
    display: inline-block;
    text-align: center;
    border-radius: 50%;
    padding: 1px;
    opacity: 0.75;
    line-height: 12px;
    transition: all 0.3s ease-in-out;
}

.image-upload-control .fc-uploaded .close i {
    font-size: 12px;
}

.image-upload-control .fc-uploaded .close:hover {
    opacity: 1;
    cursor: pointer;
}

.image-upload-control .fc-uploader span {
    font-size: 12px;
    font-weight: 500;
    color: #2b2b2b;
    display: inline-block;
    margin-left: 5px;
}

.ra-actions span i {
    margin-right: 8px;
    font-size: 14px;
}

div.moderate-public .checkbox {
    display: flex;
    align-items: flex-start;
}

.checkbox>span {
    border-color: transparent;
}

.moderate-public #moderation-toggle {
    margin-right: 5px;
    z-index: 99;
    opacity: 1;
    width: 16px;
    height: 16px;
    top: 2px;
}

.qr-code-section {
    background: #fff;
    padding: 10px;
    width: 100%;
    border-radius: 4px;
    box-shadow: 0 0 2px #DBE1E5;
}

.qr-rightsec {
    margin-left: 15px;
}

.qr-rightsec p {
    color: #2b2b2b;
    padding-right: 10px;
}

.rs-secone .rss-count {
