/* Review Banner Start */
.tb_rv_banner_wrap {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 15px 20px;
}

.tb_rv_head_text,
.tb_rv_rating_wrap {
  display: flex;
  align-items: center;
}

.tb_rv_head_text .tb_rv_head_social_text {
  margin-right: 5px;
}

.bannerPreviewCard {
  border-radius: 4px;
}

.tb_rv_banner_btn_wrap .commanBtn {
  border-color: transparent !important;
}

.tb_rv_review_count {
  font-size: 18px;
  font-weight: 600;
  margin-right: 10px;
}

.tb_rv_rview_star {
  margin-right: 10px;
}

.tb_rv_rating_wrap .tb_rv_review_count {
  display: flex;
}

@media(max-width:1366px) {
  .minicolors-theme-bootstrap .minicolors-input {
    padding-top: 9px;
    padding-bottom: 10px;
  }
}

